document.addEventListener('DOMContentLoaded', () => {
  window.sr = ScrollReveal();

  sr.reveal('.head', {
    duration: 200
  });
  sr.reveal('.first', {
    duration: 500,
    origin: 'left',
    viewOffset: { top: 0, right: 0, bottom: 300, left: 0 }
  });
  sr.reveal('.second', {
    duration: 500,
    delay: 200,
    origin: 'right',
    viewOffset: { top: 0, right: 0, bottom: 300, left: 0 }
  });
  sr.reveal('.third', {
    duration: 500,
    origin: 'left',
    viewOffset: { top: 0, right: 0, bottom: 300, left: 0 }
  });
  sr.reveal('.course-section-image', {
    duration: 500,
    origin: 'right',
    viewOffset: { top: 0, right: 0, bottom: 300, left: 0 }
  });


  $('.head-slider').slick({
    arrows: false,
    autoplay: true
  });

  $('.head-main-button').on('click', () => {
    swal({
      title: "Записаться на курс",
      text: "Введите Ваш E-mail:",
      type: "input",
      type: "input",
      showCancelButton: true,
      closeOnConfirm: false,
      animation: "slide-from-top",
      inputPlaceholder: "example@mail.com" },
      (inputValue) => {
        if (inputValue === false)
          return false;
        if (inputValue === "") {
          swal.showInputError("Введите E-mail");
          return false   }
          swal("Спасибо!", "Заявка отправлена", "success");
      });
  });

  $('.button-course').on('click', () => {
    swal({
      title: "Записаться на курс",
      text: "Введите Ваш E-mail:",
      type: "input",
      type: "input",
      showCancelButton: true,
      closeOnConfirm: false,
      animation: "slide-from-top",
      inputPlaceholder: "example@mail.com" },
      (inputValue) => {
        if (inputValue === false)
          return false;
        if (inputValue === "") {
          swal.showInputError("Введите E-mail");
          return false   }
          swal("Спасибо!", "Заявка отправлена", "success");
      });
  });



});
